@import "../../scss/Main";

.header {
  position: relative;
  display: flex;
  align-items: center;
  width: 100vw;
  height: 25vh;
  padding: 10px;

  &__title {
    font-family: "Moon Dance", cursive;
    font-size: $font-size-XL;
    color: $color-text-white;
    text-shadow: 2px 2px 2px $color-text-black;
  }

  &__img {
    position: absolute;
    background-image: url("../../assets/diceHeader.jpg");
    background-position: center;
    background-size: cover;
  }
  .dark {
    top: 0;
    left: 70%;
    width: 15%;
    height: 100%;
    z-index: -1;
  }

  .light {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.3;
    z-index: -2;
  }
}

@media only screen and (min-width: $breakpointL) {
  .header {
    &__title {
      font-size: $font-size-h1BigHeader;
    }
  }
}

@media only screen and (min-width: $breakpointXL) {
  .header {
    width: 70vw;
  }
}
